@import '../../helpers/helpers.scss';

.search {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2px;

    @include tablet() {
      max-width: 300px;
    }

    &__label {
      width: 100%;
      overflow: hidden;
    }

  &__input {
    color: #000;
    font-size: 12px;
    height: 25px;
    width: 100%;
    background-color: #fff;
    padding: 0 10px;
    border-radius: 5px;
    // transform: translateX(300px);
    transition: 1s;

    @include tablet() {
      height: 30px;
      transform: translateX(300px);
    }

    @include desktop() {
      height: 40px;
      border-radius: 10px;
    font-size: 16px;
    }

    &-move {
      transform: translateX(0);
      transition: 1s;
    }
  
    &::placeholder {
      color: #ADB5BD;
    }

    &-icon {
      background-color: #000;
      color: #fff;

      >i {
        font-size: 25px;
      }
    }
  }

  &__button {
    border-radius: 5px;
    background-color: #fff;

    @include tablet() {
      background-color: transparent;
    }
  }
}